import React from "react";
import "./Sort.scss";

const Sort = ({ onChange }) => {
  const handleChange = ({ target }) => {
    onChange({ name: target.name, value: target.value });
  };
  return (
    <div className="sort-block">
      <h3 className="sort-block_header"></h3>
      <ul className="sort-block_inputs">
        <li>
          <label>
            <input
              type="radio"
              name="sort"
              value="increase"
              onChange={handleChange}
            />
            {` -   `}
          </label>
        </li>
        <li>
          <label>
            <input
              type="radio"
              name="sort"
              value="decrease"
              onChange={handleChange}
            />
            {` -   `}
          </label>
        </li>
        <li>
          <label>
            <input
              type="radio"
              name="sort"
              value="time"
              onChange={handleChange}
            />
            {` -    `}
          </label>
        </li>
      </ul>
    </div>
  );
};

export default Sort;
